import Header from '../common/Header'
import Visual from './Visual'
import Intro from './Intro'
import Banner from './Banner'
import Project from './Project'
import Members from './Members'
import News from './News'
import Video from './Video'
import Btns from './Btns'
import Line from './../common/Line'
import Anime from './../../class/anime'
import { useEffect, useState, useRef } from 'react'

function Main() {
  const main = useRef(null)
  // useRef        
  let position = useRef([])
  //      state 
  const [index, setIndex] = useState(0)

  //  wrapping
  const getIndex = (index) => {
    setIndex(index)
  }

  const handleResize = () => {
    //  main  myScroll  
    //      .
    //  useRef  position  
    const sections = main.current.querySelectorAll('.myScroll')
    let offSetArray = []
    for (let sec of sections) offSetArray.push(sec.offsetTop)
    position.current = offSetArray
  }

  // useRef      
  //       
  const handleScroll = () => {
    let scroll = window.scrollY
    const btns = main.current.querySelectorAll('#btns li')

    position.current.map((pos, index) => {
      if (scroll >= pos) {
        for (const btn of btns) btn.classList.remove('on')
        btns[index].classList.add('on')
      }
    })
  }

  useEffect(() => {
    //     
    handleResize()
    //     getPosition  
    window.addEventListener('resize', handleResize)
    window.addEventListener('scroll', handleScroll)

    new Anime(window, {
      prop: 'scroll',
      value: position.current[index],
      duration: 500,
    })

    return () => {
      window.removeEventListener('resize', handleResize)
      window.removeEventListener('scroll', handleScroll)
    }
  }, [index])

  return (
    <div id="main__wrapper" ref={main}>
      <Header type={'main'} />
      <Visual />
      <Intro />
      <Banner />
      <Project />
      <Video />
      <News />
      <Members />
      <Line />
      <Btns getIndex={getIndex} />
    </div>
  )
}

export default Main
