const initMember = {
  members: [
    {
      name: 'Marie Fleur',
      position: 'Web Designer',
      image: '/img/about-team-img-01.jpg',
      alt: 'TEAM1',
    },
    {
      name: 'Joe Olson',
      position: 'Game Designer',
      image: '/img/about-team-img-02.jpg',
      alt: 'TEAM2',
    },
    {
      name: 'Nick Forest',
      position: 'Developer',
      image: '/img/about-team-img-03.jpg',
      alt: 'TEAM3',
    },
    {
      name: 'Mary Hart',
      position: 'Art Director',
      image: '/img/about-team-img-04.jpg',
      alt: 'TEAM4',
    },
  ],
}

const visualReducer = (state = initVisual, action) => {
  switch (action.type) {
    case 'SET_VISUAL':
      return { ...state, visual: action.payload }

    default:
      return state
  }
}

// initMember     reducer 
//    action type() payload(   ) 
const memberReducer = (state = initMember, action) => {
  // action.type   
  switch (action.type) {
    //     action.type   reducer   
    case 'SET_MEMBERS':
      return { ...state, members: action.payload }

    default:
      return state
  }
}

const youtubeReducer = (state = { youtube: [] }, action) => {
  switch (action.type) {
    case 'SET_YOUTUBE':
      return { ...state, youtube: action.payload }
    default:
      return state
  }
}

//  
const reducers = combineReducers({
  visualReducer,
  memberReducer,
  youtubeReducer,
})

// 
export default reducers
