import { useState, useEffect, useRef } from 'react'

function Community() {
  const frame = useRef(null)
  const input = useRef(null)
  const textarea = useRef(null)
  const updateInput = useRef(null)
  const updateTextarea = useRef(null)
  const showBox = useRef(null)

  const getLocalItems = () => {
    let data = localStorage.getItem('posts')

    if (data) {
      return JSON.parse(data)
    } else {
      return []
    }
  }
  const [posts, setPosts] = useState(getLocalItems)

  // post  
  const creactPost = () => {
    if (!input.current.value || !textarea.current.value) {
      alert('  ')
      return
    }
    setPosts([
      { title: input.current.value, content: textarea.current.value },
      ...posts,
    ])
    input.current.value = ''
    textarea.current.value = ''
  }

  // post  
  const deletePost = (index) => {
    setPosts(posts.filter((_, selectedPost) => index !== selectedPost))
    /* filter :       */
  }

  //    
  const enableUpdate = (index) => {
    setPosts(
      posts.map((post, selectedPost) => {
        if (selectedPost === index) post.enableUpdate = true
        return post
      })
    )
    console.log(posts)
  }

  //    
  const disableUpdate = (index) => {
    setPosts(
      posts.map((post, selectedPost) => {
        if (selectedPost === index) post.enableUpdate = false
        return post
      })
    )
    console.log(posts)
  }

  //     
  const updatePost = (index) => {
    setPosts(
      posts.map((post, selectedPost) => {
        if (selectedPost === index) {
          post.title = updateInput.current.value
          post.content = updateTextarea.current.value
          post.enableUpdate = false
        }
        return post
      })
    )
  }

  useEffect(() => {
    localStorage.setItem('posts', JSON.stringify(posts))
    frame.current.classList.add('on')
  }, [posts])

  return (
    <main className="community">
      <div className="contents" ref={frame}>
        <div className="inner">
          <h1>Community</h1>

          <section className="input__box">
            <input type="text" placeholder=" " ref={input} />
            <br />
            <textarea
              cols="30"
              rows="10"
              placeholder=" "
              ref={textarea}
            />
            <br />
            {/* CANCEL BUTTON */}
            <button
              onClick={() => {
                input.current.value = ''
                textarea.current.value = ''
              }}
            >
              CANCEL
            </button>
            {/* CREATE BUTTON */}
            <button onClick={() => creactPost()}>CREATE</button>
          </section>

          <section className="show__box" ref={showBox}>
            {posts.map((post, index) => {
              return (
                <article key={index}>
                  <div className="post">
                    {post.enableUpdate ? (
                      // 
                      <>
                        <input
                          type="text"
                          defaultValue={post.title}
                          ref={updateInput}
                        />
                        <textarea
                          defaultValue={post.content}
                          ref={updateTextarea}
                        ></textarea>
                      </>
                    ) : (
                      // 
                      <>
                        <h2>{post.title}</h2>
                        <p>{post.content}</p>
                      </>
                    )}
                  </div>

                  <ul className="btns">
                    {post.enableUpdate ? (
                      // 
                      <>
                        <li onClick={() => updatePost(index)}></li>
                        <li onClick={() => disableUpdate(index)}></li>
                      </>
                    ) : (
                      // 
                      <>
                        <li onClick={() => enableUpdate(index)}></li>
                        <li onClick={() => deletePost(index)}></li>
                      </>
                    )}
                  </ul>
                </article>
              )
            })}
          </section>
        </div>
      </div>
    </main>
  )
}

// useRef 
// post   -   
// post   
// enableUpdate  ,  or  jsx 

// creact  creactPost 
// post   =>  ,     +    

//    deletePost 
//   

//   enableUpdate  
//  post==index  post enableUpdate true
//  
// post enableUpdate true  jsx
//  jsx  updatePost
//  index == map index ?
// input, textarea useRef  value  
// enable  false 

export default Community
