import { useEffect, useState, useRef } from 'react'
import { ContactData } from '../../data/ContactData'
const path = process.env.PUBLIC_URL

function Contact() {
  const { kakao } = window

  const frame = useRef(null)
  const container = useRef(null)
  const btnBranch = useRef(null)

  const [map, setMap] = useState(null)
  //  index  
  const [index, setIndex] = useState(0)
  const [toggle, setToggle] = useState(false)

  //  
  const data = [
    {
      title: '',
      latlng: new kakao.maps.LatLng(37.55748635344998, 126.8361409, 18.75),
      //public     
      imgSrc: process.env.PUBLIC_URL + '/img/marker.png',
      imgSize: new kakao.maps.Size(50, 50),
      imgPos: { offset: new kakao.maps.Point(20, 50) },
    },
    {
      title: '  ',
      latlng: new kakao.maps.LatLng(37.27837104859774, 127.02751275725485),
      imgSrc: process.env.PUBLIC_URL + '/img/marker.png',
      imgSize: new kakao.maps.Size(50, 50),
      imgPos: { offset: new kakao.maps.Point(25, 40) },
    },
    {
      title: ' ',
      latlng: new kakao.maps.LatLng(33.46701997404598, 126.54544061331417),
      imgSrc: process.env.PUBLIC_URL + '/img/marker.png',
      imgSize: new kakao.maps.Size(50, 50),
      imgPos: { offset: new kakao.maps.Point(25, 40) },
    },
  ]

  const [mapData, setMapData] = useState(data)

  // 
  useEffect(() => {
    frame.current.classList.add('on')
    container.current.innerHTML = ''
    const options = {
      center: mapData[index].latlng,
      level: 3,
    }
    //  
    const map = new kakao.maps.Map(container.current, options)
    setMap(map)
    //   
    new kakao.maps.Marker({
      map: map,
      position: mapData[index].latlng,
      title: mapData[index].title,
      image: new kakao.maps.MarkerImage(
        mapData[index].imgSrc,
        mapData[index].imgSize,
        mapData[index].imgPos
      ),
    })
    map.setCenter(mapData[index].latlng)
    //    
    const mapTypeControl = new kakao.maps.MapTypeControl()
    map.addControl(mapTypeControl, kakao.maps.ControlPosition.TOPRIGHT)
    map.setZoomable(true)
    map.setDraggable(true)

    for (const btn of btnBranch.current.children) {
      btn.classList.remove('on')
      btnBranch.current.children[index].classList.add('on')
    }

    const mapSet = () => map.setCenter(mapData[index].latlng)
    //     
    window.addEventListener('resize', mapSet)

    return () => window.addEventListener('resize', mapSet)
  }, [index])

  return (
    <main className="contact">
      <div className="sub__visual">
        <div className="inner">
          <div className="slogan">
            <h1>
              <div className="title">CONTACT US</div>
            </h1>
            <h2>
              <div className="subtitle">GUSTAV CALATRAVA</div>
            </h2>
          </div>
        </div>
      </div>

      <div className="contents" ref={frame}>
        <section className="contact__info">
          <div className="inner">
            {ContactData.map((info, index) => {
              return (
                <article key={index}>
                  <div className="info__icon">
                    <i className={info.icon}></i>
                  </div>
                  <h1>{info.title}</h1>
                  <ul className="detail">
                    <li>{info.detail[0]}</li>
                    <li>{info.detail[1]}</li>
                  </ul>
                </article>
              )
            })}
          </div>
        </section>

        <section className="contact__form">
          <div className="inner">
            <figure>
              <img src={path + '/img/contact-form-img.jpg'} alt="" />
            </figure>

            <div className="form__wrapper">
              <h1>Send Your Message To Us</h1>
              <form>
                <input
                  type="text"
                  name="to_name"
                  required
                  placeholder="Full Name"
                />
                <input
                  type="text"
                  name="contact"
                  required
                  placeholder="Phone"
                />
                <input
                  type="text"
                  name="from_name"
                  required
                  placeholder="Email Address"
                />
                <input
                  type="text"
                  name="location"
                  placeholder="Location Of Event"
                />
                <textarea
                  name="message_html"
                  cols="30"
                  rows="5"
                  required
                  placeholder="Enter Message Here"
                />
                <div className="check">
                  <input type="checkbox" />
                  <span>
                    Save my name, email in this browser for the next time Send
                    message
                  </span>
                </div>

                <div className="button__outline">
                  <input type="submit" value="Post Comment" />
                </div>
              </form>
            </div>
          </div>
        </section>

        <section className="contact__location">
          <div className="inner">
            <h1>LOCATION</h1>

            {/*    useRef  */}
            <div id="map" ref={container}></div>

            <ul className="traffic">
              {
                // true  
                toggle ? (
                  <li
                    onClick={() => {
                      map.removeOverlayMapTypeId(kakao.maps.MapTypeId.TRAFFIC)
                      //  boolean 
                      setToggle(!toggle)
                    }}
                  >
                     TRAFFIC
                    <span> OFF</span>
                  </li>
                ) : (
                  // false  
                  <li
                    onClick={() => {
                      map.addOverlayMapTypeId(kakao.maps.MapTypeId.TRAFFIC)
                      //  boolean 
                      setToggle(!toggle)
                    }}
                  >
                     TRAFFIC
                    <span> ON</span>
                  </li>
                )
              }
            </ul>

            {/*     mapData state      */}
            <ul className="branch" ref={btnBranch}>
              <li
                onClick={() => {
                  setIndex(0)
                }}
              >
                
              </li>
              <li
                onClick={() => {
                  setIndex(1)
                }}
              >
                
              </li>
              <li
                onClick={() => {
                  setIndex(2)
                }}
              >
                
              </li>
            </ul>
          </div>
        </section>

        <section className="contact__table">
          <h1 className="visually-hidden">  </h1>
          <div className="inner">
            <h2> </h2>
            <table cellPadding="0" cellSpacing="0">
              <caption></caption>
              <colgroup>
                <col className="head"></col>
                <col></col>
              </colgroup>
              <tbody>
                <tr>
                  <th scope="col">  </th>
                  <td>   4   </td>
                </tr>
                <tr>
                  <th scope="col">  </th>
                  <td>
                        ( ) 
                  </td>
                </tr>
              </tbody>
            </table>

            <h2></h2>
            <table cellPadding="0" cellSpacing="0">
              <caption></caption>
              <colgroup>
                <col className="head"></col>
                <col></col>
              </colgroup>
              <tbody>
                <tr>
                  <th scope="col"> </th>
                  <td>031-481-0505</td>
                </tr>
              </tbody>
            </table>

            <h2> </h2>
            <table cellPadding="0" cellSpacing="0">
              <caption></caption>
              <colgroup>
                <col className="head"></col>
                <col></col>
              </colgroup>
              <tbody>
                <tr>
                  <th scope="col"></th>
                  <td>
                    23, 31, 52  (   ,  5)
                  </td>
                </tr>
                <tr>
                  <th scope="col"></th>
                  <td>
                    4  (3) &gt;  (31, 33,
                    52) [   ,  5]
                  </td>
                </tr>
                <tr>
                  <th scope="col"></th>
                  <td>
                      4     ()
                    <span>
                      *   :  10:00 ~ 19:00 /  10:00
                      ~ 20:00
                    </span>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
        </section>
      </div>
    </main>
  )
}

export default Contact
